@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300&display=swap);
*{
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  font-family: Nunito Sans;
  color: #8F71AC;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.sidebar {
  margin: 0;
  padding: 0;
  width: 70px;
  background-color: #AFAFDF;
  position: fixed;
  height: 100%;
  overflow: auto;
}
.sidebar:hover{
  width: 70px;
}

/* Sidebar links */
.sidebar a{
  display: block;
  color: black;
  padding: 25px;
  padding-top: 50%;
  padding-bottom: 50%;
  text-decoration: none;
}

/* Links on mouse-over */
.sidebar a:hover {
  background-color: #D0CFF5;
  padding: 25px;
  padding-top: 50%;
  padding-bottom: 50%;
}


/* Page content. The value of the margin-left property should match the value of the sidebar's width property */
div.content {
  margin-left: 200px;
  padding: 1px 16px;
  height: 1000px;
}

